@import "../../scss/_variable";
.showmore {
     @include flex(center);
     gap: 5px;
     margin-top: 30px;
     margin-bottom: 70px;
     @media screen and (max-width: 767px) {
          margin-bottom: 20px;
     }

     &__btn {
          font-size: 18px;
          color: $mainClr;
          cursor: pointer;
     }
     &__text {
          color: $TextClr;
          font-size: 15px;
          font-weight: 500;
          transition: 0.35s ease;
          cursor: pointer;
          &:hover {
               text-decoration: underline;
          }
     }
}
