@import "../../scss/_variable";
.e__event {
     @include flex(center);
     flex-wrap: wrap;
     padding-top: 150px;
     padding-left: 20%;
     padding-right: 20%;
     gap: 20px;
     row-gap: 24px;
     @media screen and (max-width: 767px) {
          padding-top: 100px;
          padding-left: 0%;
          padding-right: 0%;
          gap: 15px;
          row-gap: 20px;
     }

     &-name {
          color: white;
          text-transform: uppercase;
          line-height: 1.2;
     }
     &__container {
          outline: none;
          border: none;
          padding: 4px 11px;
          font-size: 13px;
          border: 0px solid $newMain;
          border-radius: 100rem;
          background-color: $newClr4;
          box-shadow: 0 0 2px $newMain;
          color: $activeClr;
          position: relative;
          overflow: hidden;
          font-weight: 400;
          transition: 0.15s ease-in;

          @media screen and (max-width: 767px) {
               font-size: 11px;
          }
          &.activeEvent {
               background-color: $background;
               color: $mainClr;
          }

          &::before {
               content: "";
               @include abs(full);
               background-color: $background;
               opacity: 0.07;
               top: 0;
               left: 0;
               width: 0;
               transition: 0.35s ease;
          }
          &:hover {
               &::before {
                    width: 100%;
               }
          }
     }
}
