@import "../../scss/_variable";

#contact {
     margin-top: 40px;
     padding-top: 30px;
     padding-bottom: 40px;
     background: $mainClr;
     @include flex(center);
     flex-direction: column;
}
.contact__container {
     @include flex(center);
     // background: $mainClr;
     border: 1px solid $background;
     gap: 30px;
     @media screen and (max-width: 767px) {
          flex-direction: column;
     }
}
.contact {
     &__face {
          width: 300px;
          height: 340px;
          position: relative;
          @media screen and (max-width: 767px) {
               height: 550px;
               width: 300px;
          }
          & iframe {
               @include abs(full);
               border: none;
          }
     }
     &__map {
          width: 370px;
          height: 340px;
          position: relative;
          & iframe {
               @include abs(full);
          }
     }
     &__icon {
          padding-right: 10px;
          padding-bottom: 10px;
     }
     &__content {
          color: $hoverClr;
          flex-wrap: wrap;
          @include flex(around);
          flex-direction: column;
          align-items: flex-start;
          width: 350px;
          height: 350px;
     }
     &__box {
          padding: 0 20px;
          @media screen and (max-width: 767px) {
               padding: 0 0px;
          }
     }
     &__description a {
          color: $hoverClr;
          transition: 0.35s ease-in-out;
          &:hover {
               color: rgb(255, 255, 255);
          }
     }
     &__number {
          display: flex;
          flex-direction: column;
          gap: 10px;
          font-size: 14px;
          justify-content: flex-start;
          padding: 30px 0;
          padding-bottom: 40px;
     }
     &__namenum {
          display: flex;
          gap: 15px;
          color: $hoverClr;
     }
     &__name {
          min-width: 180px;
          cursor: pointer;
          transition: 0.35s ease;
          color: $hoverClr;
          &:hover {
               color: white;
               text-decoration: underline;
          }
     }
}
